.cards-list {
  width: 100%;
  height: 100vh;
  padding: 1rem; }
  .cards-list__map {
    position: relative;
    width: 100%;
    height: 30%; }
  .cards-list__back-button {
    display: flex;
    align-items: center;
    max-width: 100px; }
  .cards-list__icon {
    stroke: #4f433c; }
  @media only screen and (min-width: 768px) {
    .cards-list {
      padding: 1rem 2rem; } }
  @media only screen and (min-width: 1025px) {
    .cards-list__map {
      height: 100%;
      width: 70%;
      margin-top: 1rem; } }

.cards-description-wrapper {
  height: calc(100vh - 35px - 3rem); }
  @media only screen and (min-width: 1025px) {
    .cards-description-wrapper {
      display: flex; } }

.cards-description {
  position: relative;
  width: 100%;
  height: calc(70% - 1rem);
  margin: 1rem 0;
  padding: 0;
  list-style: none;
  border-bottom: 2px solid #b0a093; }
  @media only screen and (min-width: 1025px) {
    .cards-description {
      height: 100%;
      width: calc(30% - 1rem);
      margin-right: 1rem;
      margin-bottom: 0; } }

.cards-description-substrate {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(176, 160, 147, 0.1) 92%, rgba(176, 160, 147, 0.6) 100%);
  pointer-events: none; }

.cards-description-list {
  margin: 0;
  padding: 0; }
  @media only screen and (min-width: 1025px) {
    .cards-description-list {
      margin: 0 1rem 0 0; } }

.cards-description-item {
  margin-bottom: 12px;
  padding: 12px;
  border: 2px solid #b0a093;
  background-color: transparent;
  border-radius: 10px;
  overflow: auto;
  transition: border 0.2s ease-in, background-color 0.2s ease-in; }
  .cards-description-item:hover, .cards-description-item--active {
    background-color: #faf0e8;
    border: 2px solid #4f433c;
    cursor: pointer; }
  .cards-description-item:last-child {
    margin-bottom: 0; }
  .cards-description-item__img {
    width: 120px;
    height: calc(120px * 4 / 3);
    margin-right: 8px;
    float: left;
    border-radius: 10px;
    box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.5);
    font-size: 1rem;
    object-fit: cover; }
  .cards-description-item__description {
    text-align: justify;
    font-size: 1rem;
    font-family: 'Lato', sans-serif;
    font-weight: 400; }
  .cards-description-item__title {
    font-size: 1.125rem; }
