.cards {
  display: block;
  height: calc(100vh - 70px - 1rem);
  padding: 0;
  list-style: none;
  margin: 0; }
  .cards .card {
    width: calc(100% / 6);
    height: 100%; }
  .cards--8х3 {
    max-height: calc(600px + 4px * 2); }
    .cards--8х3 .card {
      width: calc(100% / 8);
      height: 100%; }
  .cards--4х6 .card {
    width: calc(100% / 4);
    height: calc((100vh - 70px - 4px * 5 - 1rem) / 6); }

@supports (display: grid) {
